<template>
    <div>
      <common-header :title="titlename"></common-header>
      <div class="content"> 
          <div class="comment_info clearfix">
              <div>总体评价（必填）：</div>
              <div class="pos_textarea">
                <textarea placeholder="请输入"  debounce="500" @keydown="textAreaKeyDown($event,item.size)"  @input="changeInput($event, index)"></textarea>
              </div>
          </div>
          <div class="comment_result">面试结果：
              <span v-if="this.currentstate!==1">
                  <span :class="cone" @click="setClickState($event,1)">通过</span>
                  <span :class="ctwo" @click="setClickState($event,-1)">不通过</span>
                  <span :class="cthree" @click="setClickState($event,0)">待定</span>
              </span>
              <span v-else><span class="tspan">通过</span></span>
          </div>
          <div class="comment_list">
            <div  @click="showcommentlist($event)">已评评价 <img src="../assets/images/arrow_up.png" v-show="commentliststate"/><img src="../assets/images/arrow_down.png" v-show="!commentliststate"/></div>
          </div>
      </div>
      <footer class="btn_footer"  >
       <ul>
         <li @click="saveComment($event)">提交</li>
       </ul>
      </footer>
    </div>
</template>
<script>
import Header from './common/Header.vue';

export default {
  name:'ResumeComment',
  components:{
     'common-header': Header,
  },
  data () {
    return {
      titlename: '添加评语',
      commentmaxlength:1000,
      commentliststate:false,
      currentstate:0,
      states:'',
      cone:'bspan',
      ctwo:'bspan',
      cthree:'bspan',
    }
  },
  created(){

  },
   methods: {
       saveComment(e){
            //保存评语 与 状态
            console.log(e)
       },
        textAreaKeyDown(ev,maxlength){
            const value = ev.target.value;
            if(value.length >= maxlength) {
                // 删除:46 退格:8 回车:13
                if (!(ev.which == '46' || ev.which == '8'||ev.which=='37'||ev.which=='38'||ev.which=='39'||ev.which=='40')) {
                    ev.preventDefault();
                }
            }
        },
        changeInput(ev, index){
            const value = ev.target.value.substr(0,this.commentmaxlength);
            console.log(value);
        },
        showcommentlist($event){
            this.commentliststate?this.commentliststate=false:this.commentliststate=true;

        },
        setClickState(ev,setstate){
            switch (setstate) {
                case 1:
                    if(this.states===1)
                    {
                        this.states='';
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan';    
                    }else{
                        this.states=1;
                        this.cone='bspan bspanclick';
                        this.ctwo='bspan';
                        this.cthree='bspan';
                    }
                    break;
                case 0:
                    if(this.states===0)
                    {
                        this.states='';
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan';    
                    }else{
                        this.states=0;
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan bspanclick';
                    }
                break;
                case -1:
                    if(this.states===-1)
                    {
                        this.states='';
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan';    
                    }else{
                        this.states=-1;
                        this.cone='bspan';
                        this.ctwo='bspan bspanclick';
                        this.cthree='bspan';
                    }
                break;
            }
        }
   }
}
</script>

<style lang="less" scoped>
 @import "../assets/css/resumeComment.less";
</style>


